$gap: 2%;
$total-width: 70%;

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.screen {
  width: 100vw;
  height: 100vh;
  background-color: #274a60;
  //background-image: linear-gradient(180deg, #8BC6EC 0%, #9599E2 50%);
  overflow: hidden;
  display: flex;
  align-items: center;
  padding: 5vh;
  flex-direction: column;
  box-sizing: border-box;
}

.title-container {
  width: 100%;
  box-sizing: border-box;
}

.title {
  font-size: 5vh;
  font-weight: 100;
  text-transform: uppercase;
  color: white;
}

.subtitle {
  font-size: 4vh;
  font-weight: 200;
  color: white;
}

.chart-container {
  height: 100%;
  display: flex;
  align-items: flex-end;
  position: relative;
  width: $total-width;
}

.chart {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-around;
  align-items: flex-end;
  gap: $gap;
}

.column-title {
  position: absolute;
  transform: rotateZ(270deg);
  white-space: nowrap;
  transform-origin: 0% 50%;
  bottom: 0px;
  left: 50%;
  color: white;
  text-transform: uppercase;
  font-size: min(5vh, 5vw);
}

.column {
  height: 100px;
  background: red;
  position: relative;
  overflow: hidden;
  flex: 1;
  border-top-left-radius: 999px;
  border-top-right-radius: 999px;
}

.value-container {
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  position: relative;
  aspect-ratio: 1 / 1;
}

.value {
  white-space: nowrap;
  color: white;
  text-transform: uppercase;
  font-size: 4vw;
}

.circle-container {
  display: flex;
  width: 100%;
  justify-content: space-around;
  gap: $gap;
}

.circle {
  width: 100%;
  border-radius: 50%;
  aspect-ratio: 1 / 1;
}

